.action_menu {
  width: 200px;
  height: 100vh;
  position: fixed;
  left: 0;
  background-color: #586673;
  border-right: 1px solid #353940;
  box-shadow: 0 4px 8px 0 #353940, 0 6px 20px 0 #353940;
  color: #f2eeeb;
  padding: 10px;
}

.section {
  margin: 20px 0px;
  padding-bottom: 5px;
}

.section h4 {
  padding: 3px 0;
  border-bottom: 0.5px solid orange;
}

.play_button {
  margin: 10px 0;
}
.icon {
  float: left;
  width: 14px;
  margin: 4px;
}

.icon_text {
  padding: 5px;
  min-width: 90px;
}
